*{
  transition: 100ms;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #1E1E1E; */
  background-color: #151515;
  color: #8A8A8A;
}

.App {
  position: relative;
}

.browser-container > h1,
.browser-container > h2 {
  color: #4E4E4E;
  text-align: center;
  display: block;
}

.editor-img-upload,
.tui-image-editor-header-buttons,
.tui-image-editor-header-logo,
.tui-image-editor-header-logo img{
  display: none;
}

ul.editor-btns {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  top: 48px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  position: absolute;
  width: 456px;
  justify-content: space-around;
}

ul.editor-btns li {
  background-color: #2B2B2B;
  border: none;
  border-top: solid 1px #434343;
  color: #8A8A8A;
  font-family: 'Noto Sans', sans-serif;
  font-size: 12px;
  cursor: pointer;
  display: inline-block;
  font-weight: 700;
  height: 40px;
  letter-spacing: .3px;
  line-height: 40px;
  outline: none;
  padding: 0 1em;
  text-align: center;
  vertical-align: middle;
  margin: 0;
  user-select: none;
  box-sizing: border-box;
  transition: 100ms;
  width: 100%;
}



ul.editor-btns li:not(:first-child) {
  border-left: solid 1px #434343;
}

ul.editor-btns li:first-child {
  border-radius: 0 0 0 5px;
}

ul.editor-btns li:last-child {
  border-radius: 0 0 5px 0;
}

ul.editor-btns li:hover {
  color: #DDDDDD;
  background-color: #4E4E4E;
}

ul.editor-btns li:disabled {
  color: #4E4E4E;
  cursor: not-allowed;
  background-color: #2B2B2B;
}

.browser-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.image-list-container {
  width: auto;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  margin: 0.5em auto;
  justify-content: center;
}

.image-container {
  background-color: #151515;
  border: solid 1px #2B2B2B;
  margin: 0.5em;
  padding: 0.25em;
  width: 8.5em;
  box-shadow: 5px 5px 10px rgba(0,0,0,0.25);
  transform: scale(1);
  transition: transform 0.1s;
  text-align: center;
  display: block;
  position: relative;
}

.image-container:hover:not(.add-btn) {
  transform: scale(1.125);
  transition: transform 0.1s;
  z-index: 1;
  box-shadow: none;
  background-color: #2B2B2B;
}

.image-container.add-btn {
  color: #8A8A8A;
  box-shadow: none;
  background-color: #2B2B2B;
  cursor: pointer;
  transition: 100ms;
  width: 8.5em;
  height: calc(8.75em + 40px)
}

.image-container.add-btn:hover {
  color: #DDDDDD;
  background-color: #4E4E4E;
  border-color: #4E4E4E;
}

.image-container.add-btn:active {
  background-color: #151515;
  color: #4E4E4E;
  transform: scale(0.875);
  transition: transform 0.1s;
}

.image-container.add-btn > i {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  font-size: 2em;
}

.image-container .img-wrapper {
  width: 8.5em;
  height: 8.5em;
  position: relative;
  cursor: zoom-in;
}

.image-container .img-wrapper img {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  width: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.image-container .controls {
  border-radius: 0;
  margin-top: 0.25em;
}

.image-container .controls ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}

.image-container .controls ul li {
  color: #8A8A8A;
  cursor: pointer;
  display: inline-block;
  width: 33%;
  height: 40px;
  line-height: 40px;
}

.image-container .controls ul li:not(:last-child) {
  border-right: 1px solid #8A8A8A;
}

.image-container .controls ul li:hover {
  color: #DDDDDD;
  background-color: #4E4E4E;
}

.image-container .controls ul li:active {
  background-color: #151515;
  color: #4E4E4E;
}

.popup-wrapper {
  background-color: rgba(50, 50, 50, 0.25);
  backdrop-filter: blur(10px);
  color: #DDDDDD;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.popup {
  width: auto;
  height: auto;
  max-width: 90%;
  max-height: 90%;
  position: relative;
  background-color: #151515;
  border: solid 1px #8A8A8A;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  display: inline-block;
}

.popup > a {
  background-color: #151515;
  border: solid 1px #8A8A8A;
  color: #8A8A8A;
  min-width: 2em;
  min-height: 2em;
  max-width: 2em;
  max-height: 2em;
  line-height: 2em;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(0.75em, -0.75em);
  text-align: center;
}

.popup > a:hover {
  border: solid 1px #DDDDDD;
  color: #DDDDDD;
}

.popup > a:active {
  border: solid 1px #4E4E4E;
  color: #4E4E4E;
}

.popup > a i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.popup > .popup-content {
  word-wrap: break-word;
  display: flex;
}

.popup > .popup-content > img {
  max-width: 85vw;
  max-height: 90vh;
}

.popup .popup-dialog {
  margin: 1em;
  user-select: none;
}

.popup .popup-dialog h1 {
  padding: 0;
  margin: 0;
}

.popup .popup-dialog ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 1em;
}

.popup .popup-dialog ul li {
  color: #8A8A8A;
  background-color: #2B2B2B;
  border: solid 1px #2B2B2B;
  cursor: pointer;
  display: inline-block;
  width: 9em;
  height: 2.75em;
  line-height: 2.75em;
  text-align: center;
}

.popup .popup-dialog ul li:hover {
  color: #DDDDDD;
  background-color: #4E4E4E;
  border-color: #4E4E4E;
}

.popup .popup-dialog ul li:active {
  background-color: #151515;
  color: #4E4E4E;
  transform: scale(0.875);
}

.popup-image-editor {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
}